.hint {
	position: absolute;
	left: 10%;
	top: 0;
	max-width: 400px;
	padding: 10px 15px;
	background-color: var(--color-dark-red);
	color: var(--color-light-gray);
	pointer-events: none;
	border-radius: 10px;
	animation: fade-in 0.3s;
	z-index: 2;
	display: flex;
	align-items: flex-start;
	gap: 10px;
}
